import {Component, ViewChild,AfterViewChecked} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import * as screenfull from 'screenfull';
declare var $;
@Component({
    selector: 'my-app',
    template: `

       
        
    `
})
export class AgGridRndComponent implements AfterViewChecked {






    columnDefs;
    rowData;
    messages: any[] = [];
    index: number = 0;
    display;
    public arr=[];
    public style = {
        marginTop: '0px',
        width: '100%',
        height: '50%',
        boxSizing: 'border-box'
    };

   

    constructor(private http: HttpClient) {
      this.columnDefs = [
        {
          headerName: "ID",
         width:250,
         field: "id",
          resizable: true,
        
        
        },
        {
          headerName: "EMPLOYEE CITY",
          field: "empcity",
          width: 320,
          resizable: true
        },
        {
          headerName: "EMPLOYEE NAME",
          field: "empname",
          width: 320,
          resizable: true
        },
       
      ];

   



      
    }

    ngOnInit() {


    this.addMessage();
     // document.getElementById('ag-class').style.height=$("#ag-class").height()+"px";
        this.http.post('http://localhost/ang1/blog/public/getHugeData',{}).subscribe(data => {
            this.rowData = data['data'];
            this.display=true;
        });
       

        this.arr=[{
          id:1,
          tm:"02:01:01",
          quantity:10
        },
        {
          id:1,
          tm:"02:01:01",
          quantity:10
        },
        {
          id:1,
          tm:"02:01:02",
          quantity:10
        },
        {
          id:1,
          tm:"02:01:03",
          quantity:5
        },
        {
          id:1,
          tm:"02:01:03",
          quantity:5
        }
      
      
      
      ]
        let uniueArr=[];
        let volumeArray=[];

      for(var c=0;c<this.arr.length;c++){
      
          if(uniueArr.indexOf(this.arr[c].tm)==-1){
            var quat=this.arr[c].quantity;
            uniueArr.push(this.arr[c].tm);
          }
          else{
            quat=quat+this.arr[c].quantity;
            volumeArray.push(quat);
          }
      }
      console.log(uniueArr);
      console.log(volumeArray);

     







        
    }
    addMessage() {
      let detail: string = (this.index++).toString() + ': PrimeNG rocks';
      this.messages.push({severity:'info', summary:'Info Message', detail: detail});
    }
    getHeight(){
      
    }

    onFirstDataRendered(params) {
        params.api.sizeColumnsToFit();
    }

    fillLarge() {
        this.setWidthAndHeight('100%', '100%');
    }

    fillMedium() {
        this.setWidthAndHeight('80%', '60%');
    }

    fillExact() {
        this.setWidthAndHeight('400px', '400px');
    }

    setWidthAndHeight(width, height) {
        this.style = {
            marginTop: '0px',
            width: width,
            height: height,
            boxSizing: 'border-box'
        };
    }
    ngAfterViewChecked(){
  

    }
}