import { Injectable,ApplicationRef  } from '@angular/core';
import {Idle, DEFAULT_INTERRUPTSOURCES} from '@ng-idle/core';
import {Keepalive} from '@ng-idle/keepalive';
import{ConfirmationService} from '../app/generic/confirmation/confirmation.service';
@Injectable()
export class AppService {
    idleState = 'Not started.';
    timedOut = false;
    lastPing?: Date = null;
    counter:number=0;
    
   constructor(private ref: ApplicationRef ,public idle: Idle, private keepalive: Keepalive,private ConfirmationSer:ConfirmationService) {
  
   
    idle.setIdle(5);  
    idle.setTimeout(3);
    idle.setInterrupts(DEFAULT_INTERRUPTSOURCES);
    
    idle.onIdleEnd.subscribe(() => {
      this.idleState = 'No longer idle.'     
      this.ConfirmationSer.content='';
      this.ConfirmationSer.display=false;                
      this.ref.tick();

    });
    idle.onTimeout.subscribe(() => {
      this.idleState = 'Timed out!';
      this.timedOut = true;
      this.ConfirmationSer.content='';
      this.ConfirmationSer.display=false;
    });
    idle.onIdleStart.subscribe(() => this.idleState = 'You\'ve gone idle!');
    idle.onTimeoutWarning.subscribe((countdown) => {this.idleState = 'You will time out in ' + countdown + ' seconds!';
    console.log(this.idleState);
    if(idle.isIdling()){
      this.ConfirmationSer.content=this.idleState;
     this.ConfirmationSer.display=true;
    }
    else{
      this.ConfirmationSer.content='';
      this.ConfirmationSer.display=false;
    }
   
  });

    // sets the ping interval to 15 seconds
    keepalive.interval(5);

    keepalive.onPing.subscribe(() => this.lastPing = new Date());

    //this.start();
    console.log(this.idle.getInterrupts());
    }

    start(){
      if(!this.idle.isRunning() && !this.idle.isIdling()){
        console.log("wathcing start");    
      this.idle.watch();
  
    }
    }
    stop(){
      console.log("inte");
      this.idle.interrupt();
      console.log(this.idle.isRunning());
      this.idle.stop();
    }


}