import { Component,OnInit, AfterViewInit,AfterContentInit,AfterViewChecked} from '@angular/core';
import { Title }     from '@angular/platform-browser';
import {GridOptions} from "ag-grid-community";
import { HttpClient } from "@angular/common/http";



declare var $ :any;

@Component({
    selector: "my-app",
    template: `
    <button (click)="display='true'">on Show</button>
    <div style="width:100%">
    
    <ag-grid-angular
        #agGrid
        style="width:100%;height:100%"
        id="myGrid"
        class="ag-theme-fresh"
        [gridOptions]="gridOptions"
        [rowData]="rowData"
        (gridReady)="onGridReady($event)"
      ></ag-grid-angular>
   
  </div>





    `
  })
  export class AgGridRndAPiComponent implements OnInit, AfterViewChecked {
    private gridApi;
    private gridColumnApi;

    private columnDefs;
    private defaultColDef;
    private components;
    private rowBuffer;
    private rowSelection;
    private rowModelType;
    private paginationPageSize;
    private cacheOverflowSize;
    private maxConcurrentDatasourceRequests;
    private infiniteInitialRowCount;
    private maxBlocksInCache;
    private rowData:any= [];
    private dataload:boolean=false;
    private scpos:number;
 

    private display:boolean=false;
    public gridOptions=<GridOptions>{};
    private id:any;
    constructor(private http: HttpClient) {
      this.columnDefs = [
        {
          headerName: "ID",
         width:250,
         field: "id",
          resizable: true,
        
        
        },
        {
          headerName: "EMPLOYEE CITY",
          field: "empcity",
          width: 320,
          resizable: true
        },
        {
          headerName: "EMPLOYEE NAME",
          field: "empname",
          width: 320,
          resizable: true
        },
       
      ];
      
      this.defaultColDef = { resizable: true };
      this.components = {
        loadingRenderer: function(params) {
          if (params.value !== undefined) {
            return params.value;
          } else {
            return '<img src="../images/loading.gif">';
          }
        }
      };
      this.rowBuffer = 0;
      this.rowSelection = "multiple";
      this.rowModelType = "infinite";
      this.paginationPageSize = 1;
      this.cacheOverflowSize = 2;
      this.maxConcurrentDatasourceRequests = 1;
      this.infiniteInitialRowCount = 1;
      this.maxBlocksInCache = 10;
    }
  

ngAfterViewChecked(){

    // setInterval(()=>{
    //     $(".ag-body-viewport.ag-layout-normal").scrollTop(40); 
    // },1000)
    


}

ngOnInit(){
    

    this.gridOptions = {
        columnDefs: this.columnDefs,  
        enableFilter: true,
        suppressColumnVirtualisation:true,
        enableCellChangeFlash:true,
        suppressSetColumnStateEvents:true,
        rowSelection:"single"
    };

    this.callApi();
}
    onGridReady(params) {
      this.gridApi = params.api;
      this.gridColumnApi = params.columnApi;
      this.callApi();

      
    }

    callApi(){

   
  
      this.http
        .get(
          "https://raw.githubusercontent.com/ag-grid/ag-grid/master/packages/ag-grid-docs/src/olympicWinnersSmall.json"
        )
        .subscribe(data => {
          this.rowData = data;
        });
    }
         
    

    scroll(){
        console.log(this.gridOptions.api);
        this.gridApi.gridCore.rowRenderer.gridOptionsWrapper.layoutElements[1].scrollTop=this.gridApi.gridCore.rowRenderer.gridOptionsWrapper.layoutElements[1].scrollHeight-300;
    }

close1(){
//this.gridApi.destroy();



}

  



  }