import { Component,OnInit,AfterViewInit } from '@angular/core';
import {Store} from '@ngrx/store';
import {Post} from './post.model';
import  * as PostActions from './post.action';
import { Observable } from 'rxjs';
import * as screenfull from 'screenfull';
import { HttpClient } from '@angular/common/http';

declare var $ :any;
@Component({
  selector: 'full-screen',
  template: `<h1>Hello</h1><button   [name]="name"  (click)="full();">Full screen click</button>
  <button  id="myButton"   (click)="clickEve();">Full screen window</button>

  <video width="100%" controls id="myvideo">
  <source src="rain.mp4" type="video/mp4">
  <source src="rain.ogg" type="video/ogg">
  Your browser does not support the video tag.
</video>


  `,
  styleUrls: ['fullscreen.css']
})
export class FullscreenComponent implements OnInit,AfterViewInit {
  title = 'serviceWorker1';
  private name='';
  message$:Observable<string>;
  post$:Observable<Post>;
  constructor(private  httpClient:HttpClient){
  
  }
  ngOnInit(){

  }
  ngAfterViewInit(){
  





  }
  full(){
    var myButton = document.getElementById("myButton");
   
    document.addEventListener("hello", function(event) { // (1)
      
    });
  
    // ...dispatch on elem!
    let event = new Event("hello", {bubbles: true}); // (2)
    myButton.dispatchEvent(event);
   
  
    if (screenfull.enabled) {
      screenfull.toggle();
    }
  
  
    this.httpClient.put("http://127.0.0.1:3000/customers/1",

    {
    
    "name":  "NewCustomer001",
    
    "email":  "newcustomer001@email.com",
    
    "tel":  "0000252525"
    
    })
    
    .subscribe(
    
    data  => {
     
    console.log("PUT Request is successful ", data);
    
    },
    
    error  => {
    
    console.log("Rrror", error);
    // if (screenfull.enabled) {
    //     screenfull.toggle();
    // }
    this.name='pankaj';
    screenfull.exit();
   // $("#myButton").trigger("click");


    }
    
    );
   

  
  }

  clickEve(){
 
    if (screenfull.enabled) {
      screenfull.toggle();
  }
  }

  

}
 
